<template>
  <div class="nhome">
    <ul class="tags">
      <li v-for="tag in menuItem" :key="tag" v-text="tag" @click="changeList(tag)"></li>
      <li @click="todemo()" v-text="'DEMO'"></li>
    </ul>
    <artlist :artData="articleList" :type="'modal'"></artlist>
  </div>
</template>
<script>
import GetFiles from "@/common/js/getFile.js";
import artlist from "@/components/artList";
export default {
  name: "",
  components: {
    artlist
  },
  data() {
    return {
      menuItem: ["全部", "前端", "threejs", "ts", "小记", "笔记"],
      fileFun: null,
      articleList: []
    };
  },
  watch: {
    articleList(newV, oldV) {
      this.$store.dispatch("fileList", newV);
    }
  },
  created() {
    this.fileFun = new GetFiles();
    this.getFiles();
  },
  methods: {
    todemo() {
      this.$router.push("/three");
    },
    changeList(tag) {
      this.fileFun.init(tag === "全部" ? "" : tag);
      this.fileFun.getFile.then(res => {
        this.articleList = res;
      });
    },
    getFiles() {
      this.fileFun.init();
      this.fileFun.getFile.then(res => {
        this.articleList = res;
      });
    }
  }
};
</script>
<style lang="less" scoped>
.nhome {
  width: 996px;
  margin: 0 auto;
  .tags {
    display: flex;
    margin: 18px 0;
    li {
      padding: 5px 15px;
      border: 1px solid salmon;
      color: salmon;
      border-radius: 4px;
      margin-left: 15px;
      cursor: pointer;
      transition: transform 0.1s linear 0s;
      &:hover {
        transform: translateY(-2px);
        border-color: none;
        color: #ffffff;
        background: salmon;
      }
    }
  }
}
</style>


